import { Observable, of } from 'rxjs';
import {
  ExpressionOperatorGroup,
  ExpressionOperatorRowsByGroup,
  IDataDictionary,
  IExpressionConfiguratorDefaultPropertyConfig,
  RxExpressionConfigurator
} from '@helix/platform/shared/api';

export class RxShellMenuItemExpressionConfigurator extends RxExpressionConfigurator {
  commonDataDictionary$: Observable<IDataDictionary> = of([
    {
      label: 'General',
      expanded: true,
      children: [
        {
          label: 'Current person ID',
          expression: '${keywords.personId}'
        },
        {
          label: 'Current user ID',
          expression: '${keywords.userId}'
        }
      ]
    }
  ]);

  getDefaultConfig(): IExpressionConfiguratorDefaultPropertyConfig {
    return {
      ...super.getDefaultConfig(),
      operators: ExpressionOperatorRowsByGroup.get(ExpressionOperatorGroup.AllClient)
    };
  }
}
